  display: list-item;
}

/*
 * Remove outline for editable content.
 */
[contenteditable]:focus {
  outline: auto;
}

/* Tables */
/* ============================================ */
/**
1. Correct table border color inheritance in all Chrome and Safari.
*/
table {
  border-color: inherit; /* 1 */
  border-collapse: collapse;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.l-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  background-color: var(--base-color);
}
@media (min-width: 992px) {
  .l-header {
    all: unset;
  }
}
.l-header__wrapper {
  padding: clamp(0.8125rem, 0.7692307692rem + 0.1846153846vw, 1rem) 0;
}
.l-header__inner {
  max-width: 71.25rem;
  margin: 0 auto;
  padding: 0 1.25rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 992px) {
  .l-header__inner {
    max-width: unset;
    margin: unset;
    padding: unset;
    justify-content: space-between;
  }
}
@media (min-width: 992px) {
  .l-header__logo {
    margin-left: 3rem;
  }
}
@media (min-width: 992px) {
  .l-header__commonWrap {
    display: flex;
    align-items: center;
  }
}
.l-header__search-body {
  margin-right: 2rem;
}
.l-header__search {
  overflow: hidden;
}
.l-header__searchWrap {
  transform: translateX(200px);
  transition: transform 0.3s;
}
.l-header__searchWrap[aria-hidden=false] {
  transform: translateX(0px);
}
.l-header__search-icon {
  cursor: pointer;
  margin-right: 0.5rem;
}
.l-header__search-bar {
  width: 8.125rem;
  border-bottom: 1px solid #000;
}
@media (min-width: 992px) {
  .l-header__account-body {
    padding: 0 1.25rem;
  }
}
.l-header__account-image {
  width: clamp(1.25rem, 1.1057692308rem + 0.6153846154vw, 1.875rem);
  height: clamp(1.25rem, 1.1057692308rem + 0.6153846154vw, 1.875rem);
  margin-left: 1.25rem;
}
.l-header__account-image img {
  width: 100%;
  height: 100%;
}
.l-header__account-caption {
  font-size: clamp(0.625rem, 0.5961538462rem + 0.1230769231vw, 0.75rem);
  font-weight: 600;
  margin-top: 0.3125rem;
  white-space: nowrap;
}

.l-nav {
  position: fixed;
  width: 100%;
  height: 100%;
  top: clamp(3.75rem, 2.9963826367rem + 3.2154340836vw, 5rem);
  left: 50%;
  z-index: 3;
  transform: translateX(-50%);
  background-color: var(--base-color);
  visibility: hidden;
  opacity: 0;
  transition: 0.5s;
}
@media (min-width: 992px) {
  .l-nav {
    position: unset;
    width: unset;
    height: unset;
    top: unset;
    left: unset;
    transform: unset;
    background-color: unset;
    border-top: 1px solid #000;
    visibility: visible;
    opacity: 1;
  }
}
@media (min-width: 992px) {
  .l-nav__list {
    margin: 1.5rem;
    display: flex;
    justify-content: center;
  }
}
.l-nav__item {
  white-space: nowrap;
  background-color: var(--base-color);
  cursor: pointer;
  transition: 0.3s;
}
.l-nav__link-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 992px) {
  .l-nav__link-wrapper {
    display: unset;
    align-items: unset;
    justify-content: unset;
  }
}
.l-nav__link {
  display: inline-block;
  padding: 0.75rem 0 0.75rem 0.75rem;
  position: relative;
}
@media (min-width: 992px) {
  .l-nav__link {
    padding: 0 3.125rem;
    border-right: 1px solid #000;
  }
  .l-nav__link::after {
    position: absolute;
    left: 25%;
    content: "";
    width: 50%;
    height: 0.125rem;
    background-color: var(--main-color);
    bottom: -0.1875rem;
    transform: scale(0, 1);
    transform-origin: left top;
    transition: transform 0.3s;
  }
  .l-nav__link:hover .l-nav__icon {
    background-color: var(--main-color);
  }
  .l-nav__link:hover::after {
    transform: scale(1, 1);
  }
}
.l-nav__icon {
  position: absolute;
  top: 50%;
  right: 1.5rem;
  transform: translateY(-50%);
  display: inline-block;
  width: 0.5625rem;
  height: 0.4375rem;
  background-color: #000;
  clip-path: polygon(0 0, 100% 0%, 50% 100%);
  transition: 0.3s;
}
.l-nav__inner-list {
  background-color: var(--inner-color);
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}
@media (min-width: 992px) {
  .l-nav__inner-list {
    max-height: none; /* 元の高さにリセット */
    overflow: visible; /* 隠れていたものを表示 */
    visibility: hidden;
    opacity: 0;
    position: relative;
    top: 1rem;
    padding: 1.25rem;
    white-space: nowrap;
    border: unset;
    background-color: var(--base-color);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: opacity 0.3s;
  }
}
.l-nav__inner-link:first-of-type {
  padding-bottom: 1rem;
}
.l-nav__inner-link {
  display: inline-block;
  padding: 0.5rem 0 0.5rem 0.5rem;
}
@media (min-width: 992px) {
  .l-nav__inner-link {
    padding: 0;
    text-align: center;
  }
}
.l-nav__inner-link:last-of-type {
  display: inline-block;
  padding-bottom: 0.5rem;
}
@media (min-width: 992px) {
  .l-nav__inner-link:last-of-type {
    padding: 0.5rem 0 0 0;
    text-align: center;
  }
}

.l-nav[aria-hidden=false] {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 992px) {
  .l-nav__item:hover > .l-nav__inner-list,
  .l-nav__item:focus-within > .l-nav__inner-list {
    visibility: visible;
    opacity: 1;
  }
}

.l-footer {
  position: relative;
  background-image: url(/dist/img/footer-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: clamp(22.375rem, 21.6538461538rem + 3.0769230769vw, 25.5rem);
}
.l-footer__wrapper {
  padding-top: 1.5rem;
  text-align: center;
}
@media (min-width: 992px) {
  .l-footer__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: unset;
  }
}
.l-footer__info {
  margin-top: 1rem;
}
.l-footer__nav-list {
  margin-top: clamp(2.5rem, 2.2692307692rem + 0.9846153846vw, 3.5rem);
}
@media (min-width: 992px) {
  .l-footer__nav-list {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.l-footer__nav-item:not(:first-of-type) {
  margin-top: 0.5rem;
}
@media (min-width: 992px) {
  .l-footer__nav-item:not(:first-of-type) {
    margin: 0 0 0 1.5rem;
  }
}
.l-footer__copy {
  position: absolute;
  left: 50%;
  bottom: 1rem;
  transform: translateX(-50%);
}

.l-container {
  max-width: 71.25rem;
  margin: 0 auto;
  padding: 0 1.25rem;
}

.l-sp-only {
  display: block;
}
@media (min-width: 992px) {
  .l-sp-only {
    display: none;
  }
}

.l-pc-only {
  display: none;
}
@media (min-width: 992px) {
  .l-pc-only {
    display: block;
  }
}

.c-menu-btn {
  width: 2.5rem;
  height: 2.5rem;
  background-color: var(--base-color);
  position: relative;
  z-index: 2;
}
.c-menu-btn__line {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -40%);
  width: 1.5rem;
  height: 0.0625rem;
  background-color: #000;
  transition: 0.3s;
}
.c-menu-btn__line:nth-child(1) {
  top: calc(40% - 8px);
}
.c-menu-btn__line:nth-child(3) {
  top: calc(40% + 8px);
}
.c-menu-btn__text {
  position: absolute;
  left: 50%;
  bottom: 0.0625rem;
  transform: translateX(-50%);
  font-size: 0.625rem;
}

.c-menu-btn[aria-expanded=true] .c-menu-btn__line:nth-child(1) {
  top: 40%;
  transform: translate(-50%, -40%) rotate(45deg);
}
.c-menu-btn[aria-expanded=true] .c-menu-btn__line:nth-child(2) {
  opacity: 0;
}
.c-menu-btn[aria-expanded=true] .c-menu-btn__line:nth-child(3) {
  top: 40%;
  transform: translate(-50%, -40%) rotate(-45deg);
}

.c-btn--search {
  display: inline-block;
  text-align: center;
  margin-left: 0.5rem;
  padding: 0.3125rem 0;
  width: 3rem;
  font-size: 0.75rem;
  background-color: #000;
  color: var(--base-color);
}

.c-tab-btn-body {
  margin-top: clamp(2.5rem, 1.9230769231rem + 2.4615384615vw, 5rem);
}

.c-tab-btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: stretch;
}

.c-tab-btn {
  white-space: nowrap;
  padding: clamp(0.3125rem, 0.0961538462rem + 0.9230769231vw, 1.25rem) 1.25rem;
  width: clamp(5.9375rem, 2.2596153846rem + 15.6923076923vw, 21.875rem);
  text-align: center;
  box-shadow: 0px 4px 4px 0 rgba(0, 0, 0, 0.25);
  transform: 0.5s;
}
.c-tab-btn:hover {
  top: 0.25rem;
  box-shadow: none;
}
.c-tab-btn__image {
  display: flex;
  justify-content: center;
}
.c-tab-btn__text {
  font-size: clamp(0.9375rem, 0.7529761905rem + 0.2976190476vw, 1.125rem);
  font-weight: 500;
}

.c-tab-btn:not(:first-of-type) {
  margin-left: 1rem;
}

.c-tab-btn:nth-child(1) {
  background-color: var(--yoga-color);
}

.c-tab-btn:nth-child(2) {
  background-color: var(--main-color);
}

.c-tab-btn:nth-child(3) {
  background-color: var(--accent-color);
}

.c-tab-btn:nth-child(2) .c-tab-btn__text,
.c-tab-btn:nth-child(3) .c-tab-btn__text {
  color: var(--base-color);
}

.c-btn--submit {
  display: inline-block;
  padding: 1.5rem 0;
  background-color: var(--accent-color);
  color: var(--base-color);
  width: clamp(15.5625rem, 14.0336538462rem + 6.5230769231vw, 22.1875rem);
}

.c-top-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  right: 1.25rem;
  bottom: 6.25rem;
  z-index: 3;
  font-size: 0.75rem;
  width: clamp(3.125rem, 2.4038461538rem + 3.0769230769vw, 6.25rem);
  height: clamp(3.125rem, 2.4038461538rem + 3.0769230769vw, 6.25rem);
  color: var(--base-color);
  border-radius: 50%;
  background-color: var(--main-color);
}

.c-section-title {
  font-size: clamp(1.25rem, 1.0769230769rem + 0.7384615385vw, 2rem);
  font-weight: bold;
  text-align: center;
}

.c-modal {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 20;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
  transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
}
.c-modal__content {
  position: absolute;
  top: clamp(6.25rem, 4.8076923077rem + 6.1538461538vw, 12.5rem);
  left: 50%;
  transform: translateX(-50%);
}
.c-modal__image {
  position: relative;
  width: 18.75rem;
  height: 12.5rem;
  object-fit: cover;
}
@media (min-width: 768px) {
  .c-modal__image {
    width: 25rem;
    height: 18.75rem;
  }
}
@media (min-width: 992px) {
  .c-modal__image {
    width: unset;
    height: unset;
    object-fit: unset;
  }
}
.c-modal__image img {
  width: 100%;
  height: 100%;
}
@media (min-width: 992px) {
  .c-modal__image img {
    width: unset;
    height: unset;
  }
}
.c-modal__close-btn {
  position: absolute;
  top: -5%;
  right: -5%;
  width: clamp(1.875rem, 1.3991116751rem + 2.0304568528vw, 3.125rem);
  height: clamp(1.875rem, 1.3991116751rem + 2.0304568528vw, 3.125rem);
  cursor: pointer;
}

.c-icon-plus--nav, .c-icon-plus--accordion {
  position: relative;
  margin-right: 1rem;
  display: inline-block; /* 幅と高さを適用可能にする */
  vertical-align: middle;
  color: var(--accent-color);
  line-height: 1;
  width: 1em;
  height: 0.1em;
  background: currentColor;
  border-radius: 0.1em;
  transition: 0.3s;
}
.c-icon-plus--nav::before, .c-icon-plus--accordion::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: inherit;
  transform: rotate(90deg);
}
.c-icon-plus--nav.active::before, .active.c-icon-plus--accordion::before {
  width: 0;
  height: 0;
}

.c-icon-plus--accordion {
  color: #000;
}

.p-mv {
  margin-top: clamp(4.375rem, 1.336102107rem + 12.9659643436vw, 9.375rem);
  background: center center/cover no-repeat;
  width: 100%;
  height: clamp(12.5rem, 2.9822335025rem + 40.6091370558vw, 37.5rem);
  animation: Bg-animation 8s infinite ease-in-out forwards;
}
@media (min-width: 992px) {
  .p-mv {
    margin-top: 0;
  }
}

@keyframes Bg-animation {
  0% {
    background-image: url("../img/hero-banner1.jpg");
  }
  33.33% {
    background-image: url("../img/hero-banner2.jpg");
  }
  66.66% {
    background-image: url("../img/hero-banner3.jpg");
  }
  100% {
    background-image: url("../img/hero-banner1.jpg");
  }
}
.p-swiper-container {
  margin-top: clamp(3.5rem, 3.2692307692rem + 0.9846153846vw, 4.5rem);
}
.p-swiper-container .swiper-button-prev,
.p-swiper-container .swiper-button-next {
  position: absolute;
